import React, { useCallback, useMemo } from 'react'
import styled from 'styled-components'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import Page from 'components/layout/Page'
import BigNumber from 'bignumber.js'
import { Heading } from 'uikit'
import Spacer from 'components/Spacer'
import { useBondStats, useCashPriceInEstimatedTWAP } from 'hooks/useExitBoardroom'
import { getDisplayBalance } from 'utils/formatBalance'
import { useTreasury } from 'hooks/useContract'
import UnlockButton from 'components/UnlockButton'
import LaunchCountdown from 'components/LaunchCountdown'
import useTokenBalance from 'hooks/useTokenBalance'
import { QuoteToken } from 'config/constants/types'
import ExchangeCard from './components/ExchangeCard'
import ExchangeStat from './components/ExchangeStat'

const launchTime = new Date(1618502150000)

const BOND_REDEEM_PRICE = 1.01
const BOND_REDEEM_PRICE_BN = new BigNumber(10).pow(18).multipliedBy(101).dividedBy(100)

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 32px;
`

const BondHeader = () => (
  <Heading as="h1" size="lg" mb="50px" style={{ textAlign: 'center' }}>
    ðŸ’° Bonds
  </Heading>
)

const Bond: React.FC = () => {
  const { account } = useWallet()
  const contract = useTreasury()
  const bondBalance = useTokenBalance(QuoteToken.SBO)
  const cashStat = useCashPriceInEstimatedTWAP()
  const [burnable, redeemable] = useBondStats()

  const handleBuyBonds = useCallback(
    async (amount: string) => {
      const amountBig = new BigNumber(amount).times(new BigNumber(10).pow(18))
      const priceBig = new BigNumber(cashStat).times(new BigNumber(10).pow(18))
      await contract.methods.buyBonds(amountBig, priceBig).send({ from: account })
    },
    [cashStat, account, contract.methods],
  )

  const handleRedeemBonds = useCallback(
    async (amount: string) => {
      const amountBig = new BigNumber(amount).times(new BigNumber(10).pow(18))
      const priceBig = new BigNumber(cashStat).times(new BigNumber(10).pow(18))
      await contract.methods.redeemBonds(amountBig, priceBig).send({ from: account })
    },
    [cashStat, account, contract.methods],
  )
  const isBondRedeemable = useMemo(() => cashStat.gt(BOND_REDEEM_PRICE_BN), [cashStat])
  const isBondPurchasable = useMemo(() => Number(cashStat) < 1.0, [cashStat])
  const description = useMemo(() => (isBondPurchasable ? 'Burnable Dollars' : 'Redeemable Bonds'), [isBondPurchasable])

  const isLaunched = Date.now() >= launchTime.getTime()

  if (!isLaunched) {
    return (
      <Page>
        <BondHeader />
        <Wrapper>
          <LaunchCountdown deadline={launchTime} />
        </Wrapper>
      </Page>
    )
  }
  return (
    <Page>
      <BondHeader />
      {account ? (
        <>
          <StyledBond>
            <StyledCardWrapper>
              <ExchangeCard
                action="Purchase"
                fromToken={QuoteToken.SDO}
                toToken={QuoteToken.SBO}
                priceDesc={
                  !isBondPurchasable
                    ? 'SDO is over $1'
                    : `${Math.floor(100 / cashStat.toNumber() - 100)}% return when SDO > $1`
                }
                onExchange={handleBuyBonds}
                disabled={isBondRedeemable}
              />
            </StyledCardWrapper>
            <Spacer />
            <StyledCardWrapper>
              <ExchangeStat
                description={description}
                label={isBondPurchasable ? burnable?.toFixed(3) : redeemable?.toFixed(3)}
              />
              <Spacer />
              <ExchangeStat
                description="Last-Hour TWAP Price"
                label={`ðŸ’° ${QuoteToken.SDO} = $${cashStat?.toFixed(3) || '-'}`}
              />
            </StyledCardWrapper>
            <Spacer />
            <StyledCardWrapper>
              <ExchangeCard
                action="Redeem"
                fromToken={QuoteToken.SBO}
                toToken={QuoteToken.SDO}
                priceDesc={`${getDisplayBalance(bondBalance)} SDO Available`}
                onExchange={handleRedeemBonds}
                disabled={!isBondRedeemable}
                disabledDescription={!isBondRedeemable ? `Enabled when SDO > $${BOND_REDEEM_PRICE}` : null}
              />
            </StyledCardWrapper>
          </StyledBond>
        </>
      ) : (
        <UnlockButton mt="8px" fullWidth />
      )}
    </Page>
  )
}

const StyledBond = styled.div`
  display: flex;
  width: 960px;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 80%;
  }
`

export default Bond
