import React from 'react'
import styled from 'styled-components'
import Countdown, { CountdownRenderProps } from 'react-countdown'

interface LaunchCountdownProps {
  deadline: Date
}

const LaunchCountdown: React.FC<LaunchCountdownProps> = ({ deadline }) => {
  const countdownRenderer = (countdownProps: CountdownRenderProps) => {
    const { days, hours, minutes, seconds } = countdownProps
    const h = String(days * 24 + hours)
    const m = String(minutes)
    const s = String(seconds)
    return (
      <StyledCountdown>
        {h.padStart(2, '0')}:{m.padStart(2, '0')}:{s.padStart(2, '0')}
      </StyledCountdown>
    )
  }

  return (
    <StyledCard>
      <StyledCountdownTitle>Starting In...</StyledCountdownTitle>
      <Countdown date={deadline} renderer={countdownRenderer} />
    </StyledCard>
  )
}

const StyledCard = styled.div`
  width: 400px;
  background-color: ${(props) => props.theme.colors.card};
  border: 1px solid ${(props) => props.theme.colors.primary};
  box-sizing: border-box;
  padding: 36px;
  border-radius: 16px;
  box-shadow: 0px 3px 30px #d5d5d5a0;
  display: flex;
  align-items: center;
  flex-direction: column;
`

const StyledCountdownTitle = styled.p`
  font-size: 20px;
  color: ${(props) => props.theme.colors.secondary};
  margin: 0;
`

const StyledCountdown = styled.p`
  font-size: 40px;
  color: #d8325e;
  padding: 20px;
`

export default LaunchCountdown
